.settingsPage .input {
  line-height: 0;
  background: transparent;
  border: 0;
  border-radius: 1px;
  padding: 0px !important;
  border-bottom: 1px solid #a5a5a5;
  height: auto; }

.settingsPage #switch {
  padding: 0;
  list-style: none; }
  .settingsPage #switch li {
    margin: 10px;
    padding: 5px 10px;
    border-radius: 15px;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer; }
    .settingsPage #switch li .fa {
      width: 10px;
      margin-right: 8px; }
    .settingsPage #switch li.active, .settingsPage #switch li:hover {
      color: #fff;
      background-color: #5931ea; }

.settingsPage .legend {
  border-radius: 7px; }
  .settingsPage .legend .legend-title {
    padding: 10px; }

.settingsPage .settings-body .widgetTitle {
  color: #5931ea;
  font-weight: bold;
  font-size: 16px; }

.settingsPage .srow {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 10px; }
  .settingsPage .srow label {
    width: 100px;
    font-size: 14px;
    margin: 0; }
    .settingsPage .srow label.big {
      width: 50%; }
  .settingsPage .srow .value {
    font-size: 14px;
    font-weight: bold;
    width: 200px;
    text-align: left;
    margin-left: 0; }
  .settingsPage .srow .pointer {
    color: #5931ea; }
  .settingsPage .srow .left {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 50%; }
    .settingsPage .srow .left.col {
      flex-direction: column; }
    .settingsPage .srow .left div {
      font-size: 14px;
      font-weight: bold; }
    .settingsPage .srow .left span {
      display: block;
      font-size: 10px;
      color: #666; }
    .settingsPage .srow .left .fa {
      margin-right: 10px;
      font-size: 20px;
      color: #222;
      width: 16px; }
    .settingsPage .srow .left .fa-desktop {
      font-size: 14px; }
  .settingsPage .srow .right {
    display: flex;
    color: #666;
    align-items: center; }
    .settingsPage .srow .right .pointer {
      margin-left: 10px;
      color: #999;
      font-size: 16px; }

.settingsPage .add-new {
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: flex-start;
  color: #5931ea;
  font-weight: bold;
  font-size: 16px;
  line-height: 13px;
  margin-bottom: 15px; }
  .settingsPage .add-new span {
    margin-left: 10px; }

.settingsPage .sItem p {
  font-size: 16px;
  margin-bottom: 0; }
